	ul.slick-dots > li {
		width: 20px;
		height: 20px;
		position: relative;
		margin: 0 5px 5px 5px;
	}
	
	ul.slick-dots > li:before {
		content: "";
		width: 8px;
		height: 8px;
		border-radius: 50%;
		background: #D9C3AE;
		transition: all .2s ease;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
	}
	
	ul.slick-dots > li.slick-active:before {
		width: 10px;
		height: 10px;
