html,
body {
	font-family: 'Formular', sans-serif;
	font-size: 18px;
	line-height: 1.5;
	font-weight: normal;
	color: $text-color;
	background: #fff;
	min-width: 360px;
}

p {
	margin: 0 0 10px 0;
}

p:last-child {
	margin: 0;
}

a,
a:focus {
	color: #B38964;
	text-decoration: underline;
	transition: color .15s ease;
	outline: none !important;
}

a:hover {
	text-decoration: none;
}

.over-wrapper ul:not([class]) {
	list-style: none;
	margin: 15px 0;
	padding: 0;
}

.over-wrapper ul:not([class]) > li {
	margin: 0 0 10px 0;
	position: relative;
	padding: 0 0 0 63px;
}

.over-wrapper ul:not([class]) > li:before {
	content: "";
	background: #C7A383;
	width: 30px;
	height: 1px;
	position: absolute;
	left: 0;
	top: 13px;
}

.over-wrapper ol:not([class]) {
	list-style: none;
	margin: 15px 0;
	padding: 0;
	counter-reset: item;
}

.over-wrapper ol:not([class]) > li {
	margin: 0 0 10px 0;
	position: relative;
	padding: 0 0 0 94px;
}

.over-wrapper ol:not([class]) > li:before {
	counter-increment: item;
	content: counter(item);
	color: #D9C3AE;
	font-size: 40px;
	line-height: 40px;
	font-weight: bold;
	position: absolute;
	left: 0;
	top: 0;
}

.over-wrapper ol > li:nth-child(-n+10):before {
	content: "0" counter(item);
}

// Buttons

.btn,
.btn:focus {
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
	background-color: transparent;
	text-decoration: none !important;
	-webkit-border-radius: 0;
	-moz-border-radius: 0;
	border-radius: 0;
	vertical-align: top;
	border: none;
	position: relative;
	display: inline-block;
	vertical-align: middle;
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
	text-align: center;
	transition: background-color .2s ease, color .2s ease, box-shadow .2s ease, border-color .2s ease, box-shadow .2s ease, font-size .2s ease;
	-webkit-appearance: none !important;
}

.btn-login,
.btn-login:focus {
	font-size: 18px;
	line-height: 1.5;
	font-weight: normal;
	color: #39353E;
	border: 1px solid #39353E;
