// Sliders

.slick-arrow {
	position: absolute;
	z-index: 10;
	border: none;
	-webkit-appearance: none;
	width: 65px;
	height: 65px;
	background: #fff;
	border-radius: 50%;
	transition: all .3s ease;
	font-size: 0;
	line-height: 0;
	color: transparent;
	text-indent: -20000px;
	overflow: hidden;
	top: 50%;
	margin-top: -32px;
	cursor: pointer;
	outline: none !important;
	box-shadow: 10px 10px 25px rgba(60, 85, 152, 0.15);
}

.slick-arrow.slick-disabled {
	opacity: .2 !important;
}

.slick-arrow:before {
	content: "";
	width: 15px;
	height: 27px;
	background-repeat: no-repeat;
	background-position: 0 0;
	position: absolute;
	top: 50%;
	margin: -14px 0 0 0;
	transition: all .3s ease;
	opacity: 1;
}

.slick-arrow:after {
	content: "";
	width: 15px;
	height: 27px;
	background-repeat: no-repeat;
	background-position: 0 0;
	position: absolute;
	top: 50%;
	margin: -14px 0 0 0;
	transition: all .3s ease;
	opacity: 0;
}

.slick-prev:before {
	left: 22px;
	background-image: url(../images/slick-prev.svg);
}

.slick-prev:after {
	left: 22px;
	background-image: url(../images/slick-prev-hover.svg);
}

.slick-next:before {
	right: 22px;
	background-image: url(../images/slick-next.svg);
}

.slick-next:after {
	right: 22px;
	background-image: url(../images/slick-next-hover.svg);
}



.slick-prev {
	left: -10px;
}

.slick-next {
	right: -10px;
}

.slider-wrapper {
	margin: 0 -15px;
}

.slick-slide {
	margin: 0 15px;
}

.custom-slider .slick-list,
.custom-slider .slick-track {
	overflow: visible;
}

.custom-slider .slick-slide {
	transition: opacity .5s ease;
	opacity: 0;
}

.custom-slider .slick-slide.slick-active {
	opacity: 1;
}

.custom-slider .slick-arrow {
	margin-top: 0;
	top: auto;
	bottom: 100%;
	margin-bottom: 34px;
}

.custom-slider .slick-prev {
	left: auto;
	right: 110px;
}

.custom-slider .slick-next {
	right: 15px;
}

